
.radio {
  display: inline-block;
  position: relative;
  margin: 5px 0;
}
.radio__button {
  position: absolute;
  left: -9999px;
}
.radio__label {
  color: #1d3458;
  font-size: 14px;
  line-height: 18px;
  padding-left: 28px;
  cursor: pointer;
}
.radio__label:before {
    width: 18px;
    height: 18px;
    -webkit-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out;
    position: absolute;
    top: 4px;
    left: 0;
    border: 2px solid #1d3458;
    border-radius: 50%;
    content: '';
}
.radio__label:hover:before {
    background-color: #256dd3;
}
[data-peripheral='keyboard'] .radio__button:focus + .radio__label {
  -webkit-box-shadow: 0 0 0 9px white, 0 0 2px 11px #5392ff;
          box-shadow: 0 0 0 9px white, 0 0 2px 11px #5392ff;
}
.radio__button:checked + .radio__label:before {
  -webkit-box-shadow: inset 0 0 0 3px white;
          box-shadow: inset 0 0 0 3px white;
  background-color: #256dd3;
}
.radio.is-disabled .radio__label {
  color: #8c9ba5;
  cursor: not-allowed;
}
.radio.is-disabled .radio__label:before {
    background: none !important;
    border-color: #8c9ba5;
}



