
.table-header {
  width: 100%;
  color: #1d3458;
  line-height: 30px;
  background-color: white;
  padding: 10px 15px;
  text-align: left;
  font-size: 16px;
  font-weight: 300;
  border-bottom: 3px solid #1d3458;
}
@media (min-width: 768px) {
.table-header {
      padding: 15px 20px;
}
}
@media (min-width: 960px) {
.table-header {
      padding: 15px 30px;
}
}
.table {
  width: 100%;
  font-size: 12px;
}
@media (min-width: 768px) {
.table {
      font-size: 14px;
}
}
.table__row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 5px 0;
  max-width: calc(100vw - 30px);
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border-bottom: 1px solid #dfe6eb;
}
@media (min-width: 768px) {
.table__row {
      padding: 0;
}
}
.table__cell {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 100%;
          flex: 0 0 100%;
  line-height: 20px;
  padding: 5px 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (min-width: 768px) {
.table__cell {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      padding: 15px 20px;
}
}
@media (min-width: 960px) {
.table__cell {
      padding: 15px 30px;
}
}
.table__cell-label {
  display: inline-block;
  font-weight: 700;
  min-width: 40%;
  padding-right: 15px;
}
@media (min-width: 768px) {
.table__cell-label {
      display: none;
}
}
.table__head .table__row {
  display: none;
  font-weight: 700;
  background-color: white;
  border-bottom: 1px solid #1d3458;
}
@media (min-width: 768px) {
.table__head .table__row {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
}
.table__head .table__row .table__cell {
    -webkit-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out;
    position: relative;
    text-align: left;
    padding-right: 40px;
}
.table__head .table__row .table__cell:last-child {
      border: 0;
}
.table__head .table__row .table__cell:hover {
      -webkit-box-shadow: inset 0 -3px 0 #256dd3;
              box-shadow: inset 0 -3px 0 #256dd3;
}
.table__head .icon {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 15px;
  right: 20px;
  fill: #256dd3;
}
.table__body .table__row {
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
}
.table__body .table__row:nth-child(odd) {
    background-color: white;
}
.table__body .table__row:nth-child(even) {
    background-color: white;
}
.table__body .table__row:last-child {
    border-bottom: 1px solid #dfe6eb;
}
.table__body .table__row:hover {
    background-color: #f0f4f4;
    border-color: #dfe6eb;
}
.table__body .table__row:hover:last-child {
      -webkit-box-shadow: none;
              box-shadow: none;
}
.table__body .table__row.is-selected {
    position: relative;
    color: #1d3458;
    background-color: #f0f4f4 !important;
    border-color: #dfe6eb;
}
.table__body .table__row.is-selected:last-child {
      -webkit-box-shadow: none;
              box-shadow: none;
}
.table__body .table__row.is-selected:after {
      width: 5px;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: #602797;
      content: '';
}
.table__body .table__row.is-selected:hover {
      border-color: #dfe6eb;
}
.item-status:before {
  width: 16px;
  height: 16px;
  display: inline-block;
  position: relative;
  top: 2px;
  background-color: #5a6872;
  border-radius: 50%;
  margin-right: 15px;
  content: '';
}
.item-status--critical:before {
  background-color: #e62325;
}
.item-status--escalated:before {
  background-color: #fe8500;
}
.item-status--warning:before {
  background-color: #fed500;
}
.item-status--normal:before {
  background-color: #95d13c;
}



